import React, { Component } from 'react';
import './MainContainer.css';
import TorneosContainer from '../TorneosContainer/TorneosContainer';
import PartidosContainer from '../PartidosContainer/PartidosContainer';
import InfoContainer from '../InfoContainer/InfoContainer';
import Partido_tv from '../../Components/Partido_tv/Partido_tv';
import Partido_obs from '../../Components/Partido_obs/Partido_obs';

class MainContainer extends Component {

    constructor(props) {
        super(props)
        this.state = {mostrar:'true'}


    }

    componentWillMount() {
       
      
      this.updateData();
 }
     

   
      updateTime = () => {
        const {timeLineTop}=this.state;
        this.setState({
            timeLineTop: timeLineTop+1
        })
        console.log(timeLineTop)
     
      }
      componentDidMount() {
        this.timerID = setInterval(
          () => this.updateData(),
          5000
        );
      }
    
      componentWillUnmount() {
        clearInterval(this.timerID);
      }
    
    

        updateData = () => {

            fetch('http://gmatchapp.com/api/v1/partidos')//cambiado de torneos
            .then((response) => {
              return response.json()
            })
            .then((data) => {
              this.setState({ partidos: data })//cambiado de partidos

            })
        }


    render(){
      function obtenerValorParametro(sParametroNombre) {
        var sPaginaURL = window.location.search.substring(1);
         var sURLVariables = sPaginaURL.split('&');
          for (var i = 0; i < sURLVariables.length; i++) {
            var sParametro = sURLVariables[i].split('=');
            if (sParametro[0] == sParametroNombre) {
    
              return sParametro[1];
            }
          }
         return null;
        }

        var id = obtenerValorParametro('id_partido');
        var obs=obtenerValorParametro('obs');
  if (id){
    console.log("obs"+obs);
  }else{
    console.log("no id");
  }
  let infopartido_tv=[];
        
       console.log(this.state);
       const {partidos}=this.state;
  
       if(partidos){
        partidos.forEach(partido => {
        if(id==partido.id){
          infopartido_tv=partido;
        }
      });}
      console.log(infopartido_tv);
        return (
            
		<div className="mainContainer"> 
          
               {/*<div className="col-md-3 padding0 border1">
                    <TorneosContainer Torneos={Torneos}/>
        </div>*/}
                {/*<div className=" maincontainer2 padding0 ">
                    <PartidosContainer Torneos={Torneos} onClick={this.onClick}/>
      </div>*/}
                {/*<div className="col-md-3 padding0 border3">
                    <InfoContainer partido={PartidoSelecionado}/>
      </div>*/}
      {/*<div className=" maincontainer2 padding0 ">
                    <partido_tv partido={infopartido_tv}/>
      </div>*/}
{/*<Partido_tv partido={infopartido_tv}/><Partido_tv partido={infopartido_tv}/><Partido_tv Partido={infopartido_tv}/>
            </div>*/}
            {(id && obs==null) && (
              <Partido_tv partido={infopartido_tv} />
            )}
            {(id && obs) && (
              <Partido_obs partido={infopartido_tv} obs/>
            )}
        
             </div>
        );		

}
}
export default MainContainer;